/**
 * 
 */
import BaseSearch from './Search' // 
import BaseScrollTag from './ScrollTag' // 
import BaseMenu from './Menu' // 
import BaseHeaderTitle from './HeaderTitle' // 
import BaseDrawer from './Drawer' // 
import BaseEllipsis from './Ellipsis' // 
import './common.less' // 
import './bootstarp.css'
// import './styles/index.less' // iview
import '../../util/index.js'

// 
const components = [BaseSearch, BaseScrollTag, BaseMenu, BaseHeaderTitle, BaseDrawer, BaseEllipsis]

// install
const install = function (Vue) {
    if (install.installed) return
    // 
    components.map(component => Vue.component(component.name, component))
}

// VueCDN
if (typeof window !== "undefined" && window.Vue) {
    install(window.Vue)
}

export default {
  install,
  BaseSearch,
  BaseScrollTag,
  BaseMenu,
  BaseHeaderTitle,
  BaseDrawer,
  BaseEllipsis
}